.Modal {
    position: fixed;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    z-index: 120;
    backdrop-filter: blur(15px);
    background-color: #24242443;
    top: 0;
}

.ModalContent {
    border-radius: 16px;
    position: absolute;
    width: 100%;
    bottom: 100px;
}