.container {
  width: 100%;
  height: 100vh;
  display: flex;
  position: absolute;
  backdrop-filter: blur(15px);
  top: 0;
  z-index: 130;
  justify-content: center;
  align-items: center;
  background-color: #3b41482c;
  overflow: hidden;
}
.sub-container {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow:hidden;
  flex-direction: column;
  row-gap: 24px;
  height: fit-content;
}
.logImg-text{
  margin: 0;
  height: 0;
  transform: translate(0, -16px);
  font-size: 12px;
}
.logImg-container {
  width: 160px;
}

.message {
  z-index: 100;
  color: #eee;
  font-size: 20px;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  width: 230px;
  padding: 11px 34px;
  background-color: rgba(255, 0, 0, 0.509);
  margin: 0;
  border-radius: 0.25rem;
  margin-bottom: 4px;
}
  
  /* helpers/hidden.css */

  /* helpers/icon.css */
  
  /* .icons {
    display: none;
  } */
  
  .icon {
    display: inline-block;
    fill: #606468;
    font-size: 16px;
    font-size: 1rem;
    height: 1em;
    vertical-align: middle;
    width: 1em;
  }
  
  a {
    color: rgb(183, 106, 255);
    outline: 0;
    text-decoration: none;
  }
  
  a:focus,
  a:hover {
    text-decoration: underline;
  }
  
  /* modules/form.css */
  
  input {
    border: 0;
    color: inherit;
    font: inherit;
    margin: 0;
    outline: 0;
    padding: 0;
  }
  
  input[type='submit'] {
    cursor: pointer;
    width: 214px;
  }
  
  .form {
    height: auto;
    margin: -14px;
    margin: -0.875rem;
  }
/*   
  .form input[type='password'],
  .form input[type='text'],
  .form input[type='submit'] {
    width: 100%;
  } */
  
  .form__field {
    height: 50px;
    display: flex;
    margin: 14px;
    margin: 0.875rem;
  }
  
  .form__input {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
  }
  
  /* modules/login.css */
  
  .login {
    color: #eee;
  }
  
  .login label,
  .login input[type='text'],
  .login input[type='password'],
  .login input[type='submit'] {
    padding: 16px;
    padding: 1rem;
  }
  
  .login label {
    background-color: #363b41;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    padding-left: 20px;
    padding-left: 1.25rem;
    padding-right: 20px;
    padding-right: 1.25rem;
  }
  
  .login input[type='password'],
  .login input[type='text'] {
    background-color: #3b4148;
  }
  
  .login input[type='password']:focus,
  .login input[type='password']:hover,
  .login input[type='text']:focus,
  .login input[type='text']:hover {
    background-color: #434a52;
  }
  
  .login input[type='submit'] {
    background-color: rgb(183, 106, 255);
    color: #eee;
    font-weight: 700;
    text-transform: uppercase;
  }
  
  .login input[type='submit']:focus,
  .login input[type='submit']:hover {
    background-color: #1ed7c4;
  }
  
  /* modules/text.css */
  
  p {
    margin-bottom: 24px;
    margin-bottom: 1.5rem;
    margin-top: 24px;
    margin-top: 1.5rem;
  }
  
  .text--center {
    text-align: center;
  }

