
.pop-up-up {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 38.400px;
    margin-bottom: 14px;
}
.action-name, .cancel-btn {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.action-name > h1 {
    font-size: 18px;
    margin: 0;
}
.pop-up-middle {
    display: flex;
    flex-direction: row;
    height: 180px;
    column-gap: 16px;
    width: 100%;
}

.input-img {
    display: none;
}
.playlist-edit-inputs {
    width: 262px;
    row-gap: 10px;
}
.playlist-edit-name-input {
    width: 100%;
    height: 38px;
    padding: 8px;
    background: hsla(0,0%,100%,.1);
    border-radius: 5px;

}
.playlist-edit-description-input {
    height: 86px;
    width: 100%;
    padding: 8px;
    padding-bottom: 28px;
    background: hsla(0,0%,100%,.1);
    border-radius: 5px;


}
.playlist-edit-description-input > textarea {
    resize: none;
    outline: none;
    border: none;
    width: 100%;
    height: 100%;
    background-color: transparent;
    color: white;  
}
.playlist-edit-description-input > textarea::-webkit-scrollbar {
    display: none;
}
.playlist-edit-name-input > input {
    outline: none;
    border: none;
    width: 100%;
    height: 100%;
    background-color: transparent;
    color: white;
}
.img-container-pop {
    height: 180px;
    width: 180px;
}
.playlist-edit-inputs {
    display: flex;
    flex-direction: column;
}
.submit-area {
    display: flex;
    justify-content: flex-end;
    margin-top: 10px;
} 
.save-btn {
    background-color: white;
    height: fit-content;
    width: 50px;
    text-align: center;
    padding: 10px;
    border-radius: 5px;
}
.save-btn > p {
    color: black;
    font-size: 16px;
    margin: 0;
    transition: 0.5s;
}
.save-btn:hover {
    font-weight: 600;
    font-size: 17px;
    background-color: rgb(237, 237, 237);
}
#image_upload {
    display: none;
}

.outform {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
}
.editform {
    z-index: 130;
}
.cancel-btn {
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(11deg) brightness(103%) contrast(101%);
    width: 38.400px;
    height: 38.400px;
}
#input_description {
    display: none;
}


.playlist-edit-pop-up {
    padding: 0;
    row-gap: 20px;
}
.pop-up-middle {
    flex-direction: column;
    height: auto;
    justify-content: center;
    align-items: center;
    row-gap: 20px;
}
.pop-up-up {
    border-radius: 14px 14px 0 0;
    align-items: center;
    margin-bottom: 16px;
    padding-bottom: 10px;
    background-color: #3d3d3d41;
}
.edit-btn {
    margin: 0px 10px;
    font-size: 16px;
}
.editform {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #0c0c0c;
    color: white;
    border-radius: 14px;
}
.submit-area {
    justify-content: center;
}


.crecontainer {
    padding-top: 200px;
    display: flex;
    flex-direction: column;
    row-gap: 50px;
    width: 80%;
}
.creplaylistcontent {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    background: linear-gradient(to bottom, #8758FF , #290091);
    z-index: 150;
    border-radius: 20px;
}

.input-container {
    display: flex;
    justify-content: center;
    border-bottom: 2px solid rgb(187, 187, 187);
}
.input-container > input {
    outline: none;
    border: none;
    width: 80%;
    height: 100%;
    background-color: transparent;
    color: white;
    font-size: 40px;
    text-align: center;
}

.crename {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
}
.crename > p {
    margin: 0;
    color: white;
}

.btn-area {
    display: flex;
    justify-content: center;
}

.cre-btn > p {
    font-weight: 600;
    border-radius: 15px;
    margin: 0;
    padding: 10px 20px;
    background-color: aliceblue;
    font-size: 18px;
    color: black;
}
.crecancelbtn {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 18px;
    width: 60px;
    height: 60px;
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(11deg) brightness(103%) contrast(101%);

}